
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IOptionForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import OptionForm from '@/views/catalog/option/forms/OptionForm.vue'

export interface IData {
  model: IOptionForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IOptionForm) => {
      return store.dispatch('option/createForm', dataForm)
    }) as ResponseFetching<Promise<IOptionForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    OptionForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        sortOrder: 0,
        optionTypeId: 'input',
        name: 'New option',
        values: []
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'option' })
    },

    async handleSubmit () {
      // if (this.form && await this.form.validate()) {
      // }
      const data: IOptionForm = await this.saveForm(this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }

      if (this.model?.id) {
        this.$router.push({ name: 'option-update', params: { id: this.model.id } })
      }
    }
  }
})
