
import { defineComponent, PropType, reactive, ref } from 'vue'
import type { FormRules } from 'element-plus'
import { IOptionForm, IOptionType } from '@/types'

export interface IData {
  model: IOptionForm;
  isValid: boolean;
}

export default defineComponent({
  name: 'OptionForm',

  setup () {
    return {
      form: ref(),
      values: ref(),
      rules: reactive<FormRules>({})
    }
  },

  props: {
    modelValue: Object as PropType<IOptionForm>,

    isLoading: Boolean as PropType<boolean>,

    isNew: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    optionTypes (): IOptionType[] {
      return this.$store.getters['option/getTypes']
    },

    allowValueTypes (): string[] {
      const types = this.optionTypes.filter((t) => t.hasValues)

      return types.map((t) => {
        return t.id
      })
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || {
        sortOrder: 0,
        name: '123',
        optionTypeId: 'input',
        values: []
      },

      isValid: false
    }
  },

  methods: {
    async validate (): Promise<boolean> {
      let isValidValues = true

      // Если не undefined и массив форм OptionValueForm[]
      if (this.values && Array.isArray(this.values)) {
        for (const valueForm of this.values) {
          if (!await valueForm.validate()) {
            isValidValues = false
          }
        }
      }

      if (this.form) {
        // Инициация валидации для lazy-validation
        await this.form.validate()
      }

      // После вызова валидации изменит своё значение
      return this.isValid && isValidValues
    },

    addOptionValue (): void {
      this.model.values.push({
        sortOrder: 0,
        name: 'New option value',
        image: ''
      })
    },

    removeOptionValue (index: number): void {
      this.model.values.splice(index, 1)
    }
  }
})
